@charset "utf-8";

.kolonsport-site {
  header {
    width: 100vw;
    min-width: 1200px;
    left: 50%;
    transform: translateX(-50%);
    .gnb {
      height: 48px;
      border-bottom: 1px solid #ececec;
      ul {
        &.left {
          left: 0;
          li {
            padding: 0 25px;
            border-right: 1px solid #ececec;
          }
        }
        &.right {
          li {
            padding: 0 22px;
          }
        }
        li {
          line-height: 48px;
          &:last-child {
            padding-right: 33px;
          }
          a,
          span {
            font-weight: 400;
            font-size: 14px;
            &.seishin::before {
              content: '＋';
              position: relative;
              top: -1px;
              font-weight: 900;
              font-size: 11px;
            }
          }
        }
      }
      .cart a {
        position: absolute;
        top: 81px;
        right: 65px;
        display: inline-block;
        width: 23px;
        height: 23px;
        background: url('/_ui/img/kolonsport/pc/icn/icn-cart.png');
        text-indent: -9999px;
        vertical-align: middle;
        z-index: 1;
      }
      .search a {
        position: absolute;
        top: 81px;
        right: 30px;
        display: inline-block;
        width: 23px;
        height: 23px;
        background: url('/_ui/img/kolonsport/pc/icn/icn-search.png');
        text-indent: -9999px;
        vertical-align: middle;
        z-index: 1;
      }
      .searchbox {
        top: 57px;
        .search-form {
          .close {
            right: 7px;
          }
        }
      }
    }
    .menu {
      margin: 37px 0 0;
      .logo {
        position: absolute;
        top: -17px;
        left: 30px;
        width: 60px;
        height: 44px;
        margin: 0;
        background: url('/_ui/img/kolonsport/pc/logo.png') no-repeat center;
      }
      .category {
        text-align: center;
        & > li {
          margin-right: 54px;
          &:last-child {
            margin-right: 0;
          }
          & > a {
            padding-bottom: 10px;
          }
        }
        .sub-category {
          top: 52px;
          white-space: nowrap;
          .depth-wrap {
            display: inline-block;
            vertical-align: top;
            margin-top: 29px;
            text-align: left;
            .depth-item {
              strong {
                margin-top: 0;
              }
            }
          }
          .thumb-wrap {
            display: inline-block;
            vertical-align: top;
            margin-top: 29px;
            width: 359px;
            .thumb-list {
              padding: 0 13px 26px;
              overflow: hidden;
              .item {
                position: relative;
                width: 155px;
                margin-right: 23px;
                &>a {
                  .sticky {
                    width: 40px;
                    height: 40px;
                    line-height: 1.3;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    span {
                      font-size: 10px;
                      transform: scale(0.8);
                      margin: 0;
                    }
                  }
                  .txt-box {
                    display: none;
                  }
                }
                .title {
                  position: absolute;
                  top: 100%;
                  left: 0;
                  width: 100%;
                  margin-top: 10px;
                  text-align: center;
                  &>a {
                    color: #1f1f1f;
                    font-size: 12px;
                    line-height: 1.4;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;
                    display: inline-block;
                    max-width: 100%;
                  }
                }
              }
              .swiper-pagination {
                display: none;
              }
              .swiper-controls {
                display: block !important;
                .swiper-button-prev,
                .swiper-button-next {
                  margin-top: -25px;
                  width: 26px;
                  height: 25px;
                }
                .swiper-button-prev {
                  background: url("/_ui/img/kolonsport/pc/icn/icn-swipe-prev-md.png") no-repeat;
                }
                .swiper-button-next {
                  background: url("/_ui/img/kolonsport/pc/icn/icn-swipe-next-md.png") no-repeat;
                }
              }
            }
          }
        }
      }
    }
  }

  // footer
  footer {
    .kl-industry li {
      font-size: 12px;
      a {
        padding-right: 4px;
        &::after {
          content: '>';
          top: -1px;
          left: 3px;
          position: relative;
        }
      }
    }
    .f-logo {
      width: 134px;
      height: 24px;
      background: url('/_ui/img/kolonsport/pc/kolon-industry.png') no-repeat -6px 50%;
    }
    .noti {
      a {
        font-size: 12px;
        text-decoration: none;
        border-bottom: 1px solid #1f1f1f;
        padding-right: 4px;
        &::after {
          content: '>';
          top: -1px;
          left: 3px;
          position: relative;
        }
      }
    }
    .copyright {
      color: #999;
    }
    .box-sns {
      img,
      a {
        vertical-align: middle;
      }
      .award {
        width: 180px;
      }
      a {
        margin-left: 33px;
      }
      .kolonsport-app {
        margin-left: 44px;
        color: #1f1f1f;
        font-size: 11px;
        text-decoration: underline;
        height: 16px;
        line-height: 16px;
        display: inline-block;
        vertical-align: top;
      }
    }
  }

  .sub .wrap-navi {
    .sub-menu-bt {
      li {
        a {
          display: inline;
          padding-right: 5px;
          padding-bottom: 4px;
          color: #1f1f1f;
          border-bottom: 1px solid #5f5f5f;
          &::after {
            content: '>';
            position: relative;
            left: 5px;
            top: -2px;
          }
        }
        &:last-child {
          margin-top: 5px;
        }
      }
      .call-no {
        padding-bottom: 11px;
      }
      .callcenter {
        font-weight: bold;
        padding-bottom: 11px;
        &.sub {
          font-weight: normal;
          padding-bottom: 6px;
        }
      }
    }
  }
}